import type { Meta } from '@storybook/web-components';
import { StoryObj } from '@storybook/web-components';
import { html } from 'lit-html';
import { IconName } from '../../atoms/icon/icon.types';
import { InputType } from './form-input.types';
import {
  formDateTimeInputData,
  formEmailInputData,
  formNumberInputData,
  formTextInputData,
  formTextareaInputData,
} from './form-input.data';

const meta: Meta<HTMLBsFormInputElement> = {
  title: 'components/molecules/bs-form-input',
  component: 'bs-form-input',
  argTypes: {
    icon: {
      control: 'select',
      options: Object.values(IconName),
    },
    type: {
      control: 'select',
      options: Object.values(InputType),
    },
    rows: {
      if: { arg: 'type', eq: InputType.Textarea },
    },
    resizeable: {
      if: { arg: 'type', eq: InputType.Textarea },
    },
  },
  args: formTextInputData,
};

export default meta;

export const Default: StoryObj<HTMLBsFormInputElement> = {
  render: (args) =>
    html` <bs-form-input
      name="${args.name}"
      type="${args.type}"
      label="${args.label}"
      icon="${args.icon}"
      required="${args.required}"
      min="${args.min}"
      max="${args.max}"
      required-message="${args.requiredMessage}"
      invalid-message="${args.invalidMessage}"
      value="${args.value}"
      rows="${args.rows}"
      resizeable="${args.resizeable}"
    />`,
};

export const Email = {
  ...Default,
  args: formEmailInputData,
};

export const Number = {
  ...Default,
  args: formNumberInputData,
};

export const Textarea = {
  ...Default,
  args: formTextareaInputData,
};

export const DateTime = {
  ...Default,
  args: formDateTimeInputData,
};
